import React from 'react'
import {Helmet} from "react-helmet";
import PageTransition from 'gatsby-plugin-page-transitions'
import { Link } from "gatsby"
import Header from '../components/header'
import Footer from '../components/footer'
import Cta from '../components/Cta'
import lexicrea_img_travail_munch from '../images/lexicrea_img_travail_munch.png';

let pathPrefix = `/`



export default () => (
    <PageTransition>
    <div className="layoutblocksContainer">
        <Helmet>
            <meta charSet="utf-8" />
            <link rel="canonical" href="https://www.lexiquedelacreativite.com" />
            <title>Lexique de la Créativité - Talent, Travail et Créativité</title>
            <meta name="description" content="Analysez la relation entre le talent, le travail acharné et la créativité, et comment ces éléments se combinent pour favoriser l'innovation." />

        </Helmet>

        <div className="layoutblocksheaderSidebar">
            <Header/>
        </div>

        <div className="layoutblocksbodyMain">
        <Cta/>
            <div className="section">
            <div className="row align-center padding5_top">
                    <div className="small-11 medium-8 columns padding5_top">
                        <p className="letterCapitalcontent">T</p>
                        <h2>Talent</h2>
                        <p className="text-left content-project">Les capacités liées à la créativité sont appelées "facteurs d'ordre conatif" en psychologie. Plus que de capacité il s'agit d'un ensemble d'attitudes face à la vie que partagent les profils créatifs.</p>
                        <p className="text-left content-project">Les études réalisées par le professeur Gough (1979) et le professeur Gregory J. Feist (1999) suggèrent que l’ambition, la persévérance, l’indépendance, la curiosité, l’ouverture à la nouveauté, la confiance en soi, l’impulsivité, la domination, la flexibilité mentale, la sensibilité, la radicalité et la reconsidération des conventions seraient les traits de caractère définissant les personnes créatives. Il ne s’agit ici que des capacités personnelles, qui, combinées aux connaissances techniques et théoriques et à un environnement favorable vont permettre à une personne d’être créative.</p>
                        <h4>La persévérance </h4>
                        <p className="text-left content-project">Le travail créatif suppose une série d’obstacles qu’il faudra surmonter afin de parvenir à donner forme à son travail. Cette persévérance dans le travail est un des moteurs de la créativité. </p>
                        <h4>La curiosité et le goût pour l’exploration</h4>
                        <p className="text-left content-project">Le créateur aura pour habitude mentale de jouer avec les formes, les couleurs et les textures. La curiosité est difficilement mesurable scientifiquement mais c’est pourtant un trait de personnalité propre au créatif. Qu’est-ce qui pousse un individu à examiner un objet ou penser une idée sous différents angles ? La curiosité est un trait caractéristique de l’espèce humaine et même animale qui pousse les êtres vivants à étendre d’avantage les territoires connus. La curiosité amène avec elle l’émerveillement et ne cesse de poser de nouvelles questions. Cette aptitude, liée à l’imagination et à la sensibilité est une fondamentale de la créativité.</p>
                        <h4>Flexibilité et goût pour l’ambiguïté</h4>
                        <p className="text-left content-project">Guilford (1950) insiste sur l’importance des opérations mentales telle que l’analyse et la synthèse, la résolution de problème et la flexibilité intellectuelle. Selon sa théorie de l’intelligence, la créativité serait le fruit de la capacité de penser, mémoriser, des pensées convergentes et divergentes et du jugement. Cette tolérance à l’ambiguïté va permettre à l’individu de continuer d’explorer les possibilités de résolution de son problème. Aucune piste n’est exclue, aucune proposition de l’esprit ne provoque de la gêne ou du stress. Une manière de travailler cette capacité et de voyager. L’ouverture à d’autres cultures à un effet stimulant pour la créativité. </p>
                        <p className="text-left content-project">Une autre manière de forcer la flexibilité de son esprit et de l’entraîner à associer des idées éloignées afin de créer un produit nouveau. Même, et surtout, si le résultat n’a aucun sens. Cette technique permet de prendre du recul et de reconsidérer sa production en cas de blocage.</p>
                        <div className="row align-center">
                                <div className="small-11 medium-9 columns">
                                    <h2 className="project-quote">Il n’y a pas de choses de l’esprit et de choses physiques dans l’univers mais les deux ne font qu’un.
                                        <p className="project-quote-footer">Vilayanur S. Ramachandran</p></h2>

                                </div>
                            </div>
                        <p className="text-left content-project">Par flexibilité et pensée divergente, nous entendons la capacité à aborder les choses sous des angles différents. Liée à l’imagination, la pensée interdisciplinaire est la mise en relation entre des connaissances à première vue éloignée les unes des autres, c’est une des clefs de la créativité.</p>
                        <h4>Le non-conformisme</h4>
                        <p className="text-left content-project">Le créatif doit avoir un moi assez fort pour suivre ses intuitions et produire un travail qui sera le plus personnel possible. Il devra prendre en considération les autres sans que cela n’affecte son travail. La souveraineté sur son travail lui permettra de produire une œuvre réellement créative. </p>
                        <h4>L’indépendance et la prise de risque</h4>
                        <p className="text-left content-project">Innover et créer suppose une prise de risque. Bien que l’on n’ait rien à perdre et absolument tout à gagner en créant, ce travail représente un investissement important en temps (ou en argent !), donc un risque. </p>
                        <p className="text-left content-project">L’éducation et la sollicitation régulière de ces facteurs les transforment en mécanismes. Ces mécanismes changent la vision et l’approche de tous les problèmes. Selon Erika Landau la création est un jeu, et une recherche d’excellence en soi (et non contre les autres), ainsi qu’une constante remise en question. Vous comprendrez alors comment ces habitudes mentales terminent par agir à tous les niveaux de votre vie. Les ressources en action de l’ingénieur, du poète, du peintre ou du musicien reposent sur une majorité de bases communes. Seuls leurs champs d’action diffèrent.</p>
                    </div>



                    <div className="small-11 medium-8 columns padding5_top">
                        <p className="letterCapitalcontent">T</p>
                        <h2>Travail</h2>
                        <div className="row align-center">
                                <div className="small-11 medium-9 columns">
                                    <h2 className="project-quote">Non ! peu d’hommes sont doués de la faculté de voir ; il y en a moins encore qui possèdent la puissance d’exprimer.
                                        <p className="project-quote-footer">Baudelaire, <i>le peintre de la vie moderne</i></p></h2>

                                </div>
                            </div>
                        <h4>Surmonter les obstacles</h4>
                        <p className="text-left content-project">Le travail régulier, assidu et exigeant est généralement payant car il permet de chasser les mauvaises idées et de s'approcher le plus possible de l’idée créative qui a germé. Choisir la difficulté du travail acharné c’est ce qui va permettre de clarifier et de simplifier son ouvrage. C’est aussi s’assurer de se donner les moyens de produire un contenu honnête. Il faut pouvoir se perdre dans son ouvrage pour ensuite simplifier son expression et la rendre naturelle. C’est un effort long qui requiert du courage, de la persévérance et une forme d’ascèse.</p>
                        <h4>Ne pas compter les heures</h4>
                        <p className="text-left content-project">Encore une fois ici, l’image du créateur fainéant qui créé sous le coup de l’inspiration soudaine est écornée. Nous ne naissons pas instruits (mais nous sommes paresseux par nature !). Bien que certains individus aient des facilités presqu’innées dans leurs domaines, ils ne pourront se passer de travail s’ils souhaitent repousser les limites de leur créativité.  Celui qui a la création comme nécessité vitale ne comptera pas les heures qu’il dédiera à son Art. Il s’engagera alors dans un combat obstiné avec la matière créative émanant de son être et il lui sera impossible de s’en défaire.</p>
                        <p className="text-left content-project">Le travail abandonné agira comme un feu qu’on n’a pas nourri : il restera des braises rougissantes, mais le créateur devra redoubler d’effort pour lui redonner ses flammes et son ardeur. La création ne peut exister sans travail et sans technique. Cézanne évoque ses difficultés à représenter les sensations que lui inspirent la nature : Il mesure à quel point ses moyens sont minces et comment il doit encore développer son talent s’il veut atteindre la richesse naturelle :</p>
                        <div className="row align-center">
                            <div className="small-11 medium-9 columns">
                                <h2 className="project-quote">"… Je deviens, comme peintre, plus lucide devant la nature, mais… Chez moi la réalisation de mes sensations est toujours très pénible. Je ne peux arriver à l’intensité qui se développe à mes sens, je n’ai pas cette magnifique coloration qui anime la nature. Ici, au bord de la rivière, les motifs se multiplient, le même sujet vu sous un angle différent offre un sujet d’étude du plus puissant intérêt, et si varié que je crois que je pourrais m’occuper pendant des mois sans changer de place en m’inclinant tantôt plus à droite, tant plus à gauche."
                                    <p className="project-quote-footer">Lettre à Louis Aurenchen, Paris Grasset, 1937<i>Correspondance de Paul Cézanne</i></p></h2>

                            </div>
                        </div>
                        <p className="text-left content-project">La persévérance est avec, d’autres facteurs cognitifs, émotionnels et environnementaux un des piliers de la créativité.</p>
                        <h4>Emotions positives et persévérance</h4>
                        <p className="text-left content-project">L’idée d’une vie dédiée au travail n’est pas très séduisante mais si ce travail est celui de la créativité, il devient une activité stimulante et tournée vers la vie. Rainer Maria Rilke, poète, écrivait à Auguste Rodin :</p>
                        <div className="row align-center">
                            <div className="small-11 medium-9 columns">
                                <h2 className="project-quote">« C’est souvent dans les choses d’apparence modeste qu’on apprend le plus. Le travail est mystérieux. Il accorde beaucoup aux patients et aux simples ; il refuse aux pressés et aux vaniteux ; il accorde à l’apprenti et refuse à l’élève : et un jour la merveille naît des mains du modeste travailleur. »
                                    <p className="project-quote-footer">Lettre de Rainer Maria Rilke, poète, à Auguste Rodin</p></h2>
                            </div>
                        </div>
                        <h4>L'oeuvre inachevée</h4>
                        <p className="text-left content-project">Pendant sa réalisation, l’ouvrage va continuer son évolution car les étapes précédentes restent actives. L'expérience permet l'émergence de nouvelles idées. Nombreux sont les artistes qui avancent qu’une œuvre mûrit lentement pendant le travail de création et continue d’évoluer en dépit de la première idée. Cette vision peut être le point de départ d’une œuvre totalement différente à l’idée initiale.  Il peut arriver aussi que la vision et les moyens nécessaires pour sa réalisation soient si clairs que la réalisation de l’œuvre est immédiate.</p>
                        <p className="text-left content-project">Le plus difficile dans l’élaboration est de considérer le travail comme fini, et de réussir à s’en convaincre ! Cette capacité varie d’un créateur à l’autre. Certains seront convaincus que l’œuvre est terminée et pourront presque l’oublier pour passer à autre chose. D’autres, comme Flaubert ou Léonard de Vinci étaient incapables d’achever leurs œuvres…   Il n’y a donc pas de durée précise pour cette phase, il n’y a pas de règle. Une œuvre est vivante et peut évoluer si son créateur n’est pas satisfait, ou s’il continue d’accumuler du savoir et de l’expérience technique lui permettant d’affiner encore son expression (dissonance entre vision et réalité du résultat dans le réel).</p>
                        <p className="text-left content-project">Ci-dessous, un exemple concret avec l’évolution dans le temps d’une œuvre de Munch, vampire dans la forêt.</p>
                        <img className="articleIllustration" src={lexicrea_img_travail_munch} alt="oeuvre inachevée munch" />
                    </div>

                </div>
            </div>
            <p className="forward">
            <Link to="/U_livre_lexique_creativite_universalite_et_creativite/">
                <p className="letterCapital">U</p>
                <p>Universalité</p>
                <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-arrow-narrow-right" width="44" height="44" viewBox="0 0 24 24" stroke-width="1" stroke="#282524" fill="none" stroke-linecap="round" stroke-linejoin="round">
                    <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                    <path d="M5 12l14 0" />
                    <path d="M15 16l4 -4" />
                    <path d="M15 8l4 4" />
                </svg>
            </Link>


        </p>
        </div>

    </div>






</PageTransition>
)

